.profile-sidebar {
  display: flex;
  flex-direction: column;
  padding: 1rem;
  width: 30%;
  align-items: flex-start;
  justify-content: flex-start;
  height: auto;
  color: #ABA8A7;
  box-sizing: border-box;
}
.profile-sidebar-content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  width: 100%;
  margin-top: 1rem;
  gap: 0.25rem;
}
.profile-sidebar-divider {
  width: 100%;
  height: 1px;
  background-color: #ABA8A7;
  margin: 0.5rem 0;
}
.profile-sidebar-add-track-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 0.5rem;
  background-color: #B51811;
  color: #FFFFFF;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.3s;
}
.profile-sidebar-add-track-button:hover {
  background-color: #D32F2F;
}
.profile-sidebar-add-track-button:active {
  background-color: #B51811;
}
.add-track-menu {
  margin-top: 1rem;
  padding: 1rem;
  border: 1px solid #B51811;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #ABA8A7;
}
.add-track-main-label {
  margin-bottom: 1rem;
  font-size: 1.25rem;
  font-weight: bold;
  text-align: center;
}
.add-track-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 100%;
}
.add-track-label {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-size: 0.875rem;
  width: 100%;
}
.add-track-label input {
  width: 100%;
  padding: 0.5rem;
  border: 1px solid #ddd;
  font-size: 0.875rem;
  border-radius: 4px;
}
.add-track-label input[type="number"] {
  -moz-appearance: textfield;
  -webkit-appearance: none;
  appearance: none;
}
.add-track-label input[type="number"]::-webkit-inner-spin-button,
.add-track-label input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.add-track-row {
  display: flex;
  gap: 1rem;
  justify-content: space-between;
  width: 100%;
}
.add-track-row .add-track-label {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.add-track-row .add-track-label input {
  max-width: 4rem;
  text-align: center;
}
.add-track-row .add-track-label input[name="year"] {
  max-width: 6rem;
}
.submit-track-button {
  margin-top: 1rem;
  padding: 0.5rem 1rem;
  background-color: #B51811;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.875rem;
  transition: background-color 0.3s ease;
  text-align: center;
}
.submit-track-button:hover {
  background-color: #D32F2F;
}
.submit-track-button:active {
  background-color: #B51811;
}
.error-message {
  color: #B51811;
  font-size: 0.875rem;
  margin-top: 0.5rem;
  text-align: center;
  font-weight: 600;
}
.success-message {
  color: #2E7D32;
  font-size: 0.875rem;
  margin-top: 0.5rem;
  text-align: center;
  font-weight: 600;
}
